.card{
    width: 75px;
    height: 75px;
    padding: 5px;
    border: 1px solid #000;
    border-radius: 4px;
    font-size: small;
}
.card:hover{
    cursor: pointer;
    background-color: #9ed974;
}
.selectedCard{
    width: 75px;
    height: 75px;
    padding: 5px;
    border: 1px solid #000;
    border-radius: 4px;
    font-size: small;
    background-color: #9bec4c;
}